import React, { useState, useEffect, useCallback} from "react";
import styled from "styled-components";
import {Link} from 'react-router-dom'
import { useWeb3React } from '@web3-react/core';
import classes from "./Dome.module.css";
import { getContract } from './Helpers/contract';
import {  getContract2 }  from './Helpers/contractPlex';



const StyledPlex = styled.span`
	color: #202020;
	font-family: 'Akaya Telivigala', cursive;
	font-size: 90px;
	transition: all 0.2s ease;
	&:hover{
		transform: scale(1.1);
	}  
`
const StyledHeaderContainer = styled.div`
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 87.5px;
`
const Section = styled.section`
	min-height:500px;
	width: 100%;
	position: relative;
	background-color: #f2f3ff;
`

const StakeUs = (provider) => {
	const [balance,setBalance]= useState("")
	const {account,library} = useWeb3React();
	const {chainId} = useWeb3React();
	
	//connector, library, chainId, account, activate, deactivate
	const web3reactContext = useWeb3React(); 

	//useEffect balance
	useEffect(() => {
		library?.getBalance(account).then((result)=>{
		  setBalance(result/1e18)
		})
		},);

	

	//web3react
	const writeToContractUsingWeb3React = async () => {
		try {
			const randomNumber = Math.floor(Math.random() * 100000);
			const myContract = getContract(web3reactContext.library, web3reactContext.account);
			const overrides = {
				gasLimit: 230000
			};
			const response = await myContract.storeNum(randomNumber, overrides);
			console.log(response);
			alert('write ' + randomNumber);
		} catch (ex) {
			console.log(ex);
			alert(ex);
		}
	};

const Card = (props) => {
  return (
    <div className={`${classes.card} ${props.className}`}>{props.children}</div>
  );
};


    return (
	
	
        <Section >
            <StyledHeaderContainer>
              
              <StyledPlex><Link to ="/">Plexus</Link> </StyledPlex>
            </StyledHeaderContainer>
           <Card className={classes.dome}>
				<h2>USE PANEL Presale</h2>	<br />	
					{web3reactContext.account ? <p>User account: {web3reactContext.account.substring(0, 6)}....{web3reactContext.account.substring(web3reactContext.account.length - 5)}</p> : <p>Not connected</p>}
				<h3>balance: {balance}</h3> 	
				         
				<div className="flex flex-col space-y-2 items-start pt-10  ">
								
					<button
						className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded   "
						onClick={writeToContractUsingWeb3React}
						>
						press get number 
					</button>
					 		<button className="bg-purple-500 hover:bg-purple-700 text-white font-bold py-2 px-4 rounded "
								onClick={writeToContractUsingWeb3React}
								>Buy Token
							</button>
				</div>
			
	   
	

	
      	&nbsp; &nbsp;

     
      <p>timeHold:</p> 
	  cId: {chainId}

	  
			</Card>
		   
        </Section>
		
    );
};

export default StakeUs;